// /////////////////////////
// @@ HEADER REGION
// ////////////////////////

.r-header {
	position: relative;
  background: {
    image: url("../images/header.jpeg");
    repeat: no-repeat;
		position: center;
		attachment: fixed;
    size: cover;
  }
  
	height: 50rem;

	@include mq(desktop) {
		height: 75rem;
	}
	
	&__overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(map-get($colors, black), .65);
    z-index: 2;
  }

  &__inner {
    height: 100%;
    display: flex;
    justify-content: center;
		align-items: center;
		position: relative;
		z-index: 3;
  }

  &__brand {
    text-align: center;
    display: flex;
    justify-content: center;
		align-items: center;
		
		@include mq(desktop) {
			display: none;
		}
  }

  &__title {
    color: map-get($colors, accent-two);
    font: {
      weight: $fw-bold;
    }
  }

  &__slogan {
    font: {
      size: 2.5rem
    }
  }

  &__logo {
    width: 35%;
    max-width: 20rem;
  }
}